import { createAsyncThunk, createSlice, PayloadAction } from "@reduxjs/toolkit";
import axios from "axios";
import { stat } from "fs";
import { addProductList as add } from "./addShoppingCartItem";
import { clearProductList as clear } from "./clearShoppingCartItem";
import { productList as get } from "./getShoppingCart";

import { checkOrderItem } from "../order/checkOut";

interface ShoppingCartState {
  loading: boolean;
  error: undefined | null;
  data: any[];
}

const initialState: ShoppingCartState = {
  loading: true,
  error: null,
  data: [],
};

// 获取购物车商品
export const getShoppingCart = createAsyncThunk(
  "shoppingCart/getShoppingCart",
  async (jwt: string) => {
    const { data } = await axios.get(`/api/shoppingCart/getShoppingCart`, {
      headers: {
        Authorization: `bearer ${jwt}`,
      },
    });
    // return data.shoppingCartItems;
    return get;
  }
);

// 添加购物车
export const addShoppingCartItem = createAsyncThunk(
  "shoppingCart/addShoppingCartItem",
  async ({ jwt, touristRouteId }: { jwt: string; touristRouteId: string }) => {
    // const { data } = await axios.post(
    //   `/api/shoppingCart/addShoppingCartItem`,
    //   {
    //     touristRouteId,
    //   },
    //   {
    //     headers: {
    //       Authorization: `bearer ${jwt}`,
    //     },
    //   }
    // );
    // return data.shoppingCartItems;
    return add;
  }
);

// 清空购物车
export const clearShoppingCart = createAsyncThunk(
  "shoppingCart/clearShoppingCart",
  async ({ jwt, itemsId }: { jwt: string; itemsId: number[] }) => {
    await axios.delete(`/api/shoppingCart/clearShoppingCartItem`, {
      headers: {
        Authorization: `bearer ${jwt}`,
      },
    });
    return clear;
  }
);

export const checkOut = createAsyncThunk(
  "shoppingCart/checkOut",
  async (jwt: string) => {
    // const { data } = await axios.post("/api/shoppingCart/checkOut", null, {
    //   headers: {
    //     Authorization: `bearer ${jwt}`,
    //   },
    // });
    // return data;
    return checkOrderItem;
  }
);

export const shoppingCartSlice = createSlice({
  name: "shoppingCart",
  initialState,
  reducers: {},
  extraReducers: {
    [getShoppingCart.pending.type]: (state) => {
      state.loading = true;
    },
    [getShoppingCart.fulfilled.type]: (state, action) => {
      state.loading = false;
      state.data = action.payload;
      state.error = null;
    },
    [getShoppingCart.rejected.type]: (state, action) => {
      state.loading = false;
      state.error = action.payload;
    },
    [addShoppingCartItem.pending.type]: (state) => {
      state.loading = true;
    },
    [addShoppingCartItem.fulfilled.type]: (state, action) => {
      state.loading = false;
      state.data = action.payload;
      state.error = null;
    },
    [addShoppingCartItem.rejected.type]: (state, action) => {
      state.loading = false;
      state.error = action.payload;
    },
    [clearShoppingCart.pending.type]: (state) => {
      state.loading = true;
    },
    [clearShoppingCart.fulfilled.type]: (state, action) => {
      state.loading = false;
      state.data = [];
      state.error = null;
    },
    [clearShoppingCart.rejected.type]: (state, action) => {
      state.loading = false;
      state.error = action.payload;
    },
    [checkOut.pending.type]: (state) => {
      state.loading = true;
    },
    [checkOut.fulfilled.type]: (state, action) => {
      state.data = [];
      state.loading = false;
      state.error = null;
    },
    [checkOut.rejected.type]: (state, action) => {
      state.loading = false;
      state.error = action.payload;
    },
  },
});
